import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import LoginDiv from "../../style/UserCSS";

import firebase from "../../firebase";
import { useSelector } from "react-redux";
import { RootState } from "../../Reducer/store";

type ErrorType = {
  code: string;
};

const Login = () => {
  const navigate = useNavigate();
  const user = useSelector((state: RootState) => state.user);
  const [email, setEmail] = useState("");
  const [PW, setPW] = useState("");

  const signInFunc = async (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    e.preventDefault();
    if (!(email && PW)) {
      return alert("   ");
    }
    try {
      await firebase.auth().signInWithEmailAndPassword(email, PW);
      navigate("/");
    } catch (error: unknown) {
      const err = error as ErrorType;

      if (err.code === "auth/user-not-found") {
        alert("   .");
      } else if (err.code === "auth/wrong-password") {
        alert("  .");
      } else {
        alert(" .");
      }
    }
  };

  useEffect(() => {
    if (user.accessToken) {
      alert("      .");
      navigate("/");
    }
  }, []);

  return (
    <LoginDiv>
      <form>
        <label htmlFor="email"></label>
        <input id="email" type="email" value={email} required onChange={(e) => setEmail(e.currentTarget.value)} />
        <label htmlFor="password"></label>
        <input id="password" type="password" value={PW} onChange={(e) => setPW(e.currentTarget.value)} />
        <button onClick={(e) => signInFunc(e)}></button>
        <button
          onClick={(e) => {
            e.preventDefault();
            navigate("/register");
          }}
        >
          
        </button>
      </form>
    </LoginDiv>
  );
};

export default Login;
